<template>
  <div class="chapter-detail">

    <el-card class="model-selector">
      <div class="model-header">
        <h3 style="color: #409eff;margin: 0"> </h3>
      </div>
    <div class="model-selection-container">
      <div class="selection-tip">
        <el-icon><Warning /></el-icon>
        1AI
      </div>
     <!--  -->
      <el-checkbox-group 
        v-if="showModelSelector"
        v-model="selectedModels"
        class="model-checkbox-group"
        :disabled="isChatting">
        <el-checkbox-button
          v-for="model in availableModels"
          :key="model"
          :label="model"
          class="model-btn">
          {{ model }}
        </el-checkbox-button>
      </el-checkbox-group>
      
      <!--  -->
      <div v-else class="selected-models">
        <el-tag
          v-for="model in selectedModels"
          :key="model"
          type="info"
          class="model-tag"
        >
          <el-icon><MagicStick /></el-icon>
          {{ model }}
        </el-tag>
      </div>
    </div>
      <!--  -->
      <div v-if="showAnswerButton" style="margin-top: 15px;">
        <el-button
          type="success"
          :disabled="!canSend"
          class="compact-btn"
          @click="handleAnswer"
        >
          <el-icon><MagicStick /></el-icon>
          
        </el-button>
      </div>
    </el-card>

   

    <!--  -->
    <div class="chat-container">
      <!--  -->
      <div
        ref="messageBox"
        class="message-list"
      >
        <div 
          v-for="(msg, index) in messages"
          :key="index"
          class="message-item"
        >
          <div class="user-message">
            <el-icon class="avatar">
              <User />
            </el-icon>
            <div class="content-box">
              <div class="message-content">
                {{ msg.content }}
              </div>
            </div>
          </div>
          
          <div class="ai-responses">
            <el-card
              v-for="model in selectedModels"
              :key="model"
              class="response-card"
            >
              <template #header>
                <div class="model-name">
                  {{ model }}
                </div>
              </template>
              <div class="response-content">
                <div v-html="renderMarkdown(getModelResponse(msg.id, model))" />
                <!--  -->
                <div class="rating-wrapper" v-if="msg.hasAllResponses">
                  <div class="rating-tip">1-100</div>
                  <el-input-number 
                    v-model="msg.scores[model]"
                    :min="1"
                    :max="100"
                    :step="1"
                    size="small"
                    @change="(val) => handleScoreChange(msg.id, model, val)"
                    class="number-rating"
                  />
                  <!--  -->
                  <div v-if="msg.scores[model] !== null" class="reason-input">
                    <el-input
                      v-model="msg.reasons[model]"
                      type="textarea"
                      :rows="2"
                      placeholder=""
                      class="mt-2"
                    />
                    <el-button
                      type="primary"
                      size="small"
                      class="mt-2"
                      :disabled="!msg.reasons[model]?.trim()"
                      @click="handleSubmitReason(msg.id, model)"
                    >
                      
                    </el-button>
                  </div>
                </div>
              </div>
            </el-card>
          </div>
        </div>
      </div>
    </div>
    <div class="improve-section" v-if="scoreSubmitted">
      <el-card class="improve-card">
        <h4> </h4>
        
        <!--  -->
        <div class="chat-container">
          <div class="chat-messages">
            <div 
              v-for="(msg, index) in improveHistory"
              :key="index"
              :class="['message', msg.role]"
            >
              <div class="role-icon">{{ msg.role === 'user' ? '' : '' }}</div>
              <div class="content" v-html="renderMarkdown(msg.content)" />
            </div>
          </div>
          
          <div class="chat-input" v-if="!hasCompleted">
            <v-md-editor
              v-model="improveInput"
              :disabled-menus="[]"
              height="150px"
              placeholder="Markdown..."
              @keydown.enter="handleKeydown"
              class="md-editor-custom"
            />
            <el-button
              type="primary"
              @click="handleImproveSend"
              :loading="isImproving"
              style="margin-top: 10px;"
            >
              
            </el-button>
          </div>
        </div>
      </el-card>
    </div>

   <div v-if="hasCompleted" class="completion-message">
      <el-alert title="" type="success" :closable="false">
        <template #default>
          <span> </span>
        </template>
      </el-alert>
    </div>
  </div>
</template>

<script setup>
// 
import { ref, reactive, onMounted, computed, nextTick} from 'vue'  // inject
import { ElMessage, ElMessageBox } from 'element-plus'
import { getAvailableModels, multiModelChat ,chatAnswer} from '@/api/askyou'
import { useRoute} from 'vue-router'
import { MagicStick } from '@element-plus/icons-vue'  // Refresh
import { updateModelScore,updateStudentReason } from '@/api/askyou'  // 
import { updateProgressStatus } from '@/api/progress'
// ... existing imports ...
import VMdEditor from '@kangc/v-md-editor';
import '@kangc/v-md-editor/lib/style/base-editor.css';
import githubTheme from '@kangc/v-md-editor/lib/theme/github.js';
import '@kangc/v-md-editor/lib/theme/style/github.css';

VMdEditor.use(githubTheme);

// setup
const handleKeydown = (e) => {
  if (!e.shiftKey) {
    e.preventDefault();
    handleImproveSend();
  }
};
const isImproving = ref(false)
const scoreSubmitted = ref(false)



const route = useRoute()
const messages = reactive([])
const selectedModels = ref([])
const isChatting = ref(false)
const availableModels = ref([])

// 
const question = ref(route.query.question || '')
const questionId = ref(route.query.questionId || null)
const userId = ref(route.query.userId || null)

// 
const improveInput = ref('')
const improveHistory = ref([])
// 
const hasCompleted = computed(() => 
  improveHistory.value.some(msg => 
    msg.role === 'assistant' && 
    msg.originalData?.flag // 
  )
)

// 
const handleImproveSend = async () => {
  try {
    isImproving.value = true
    improveHistory.value.push({
      role: 'user',
      content: improveInput.value
    })

    const { data } = await chatAnswer({
      prompt: improveInput.value,
      userId: Number(userId.value),  // 
      courseId: Number(route.params.courseId),  // 
      chapterId: Number(route.params.chapterId),  // 
      questionId: Number(questionId.value),  // 
      question: question.value  // 
    }, {
      timeout: 300000
    })

    // AI
    // 
    improveHistory.value.push({
      role: 'assistant',
      content: data.data.response,
      originalData: data.data // 
    })

    // 
    improveInput.value = '' 

    // flag
    if (data.data.flag) {
      await updateProgressStatus({
        userId: Number(route.query.userId),
        courseId: Number(route.params.courseId),
        chapterId: Number(route.params.chapterId),
        step: 2
      })
      
      // 
      ElMessageBox.alert('', '', {
        confirmButtonText: '',
        type: 'success'
      })
      
      // 
      // scoreSubmitted.value = false
    }
  } catch (error) {
    ElMessage.error(': ' + error.message)
  } finally {
    isImproving.value = false
  }
}
// 
onMounted(() => {
  fetchModels().then(() => {
    if (question.value) { // question
      sendMessage()
    }
  })
})
// 
const handleScoreChange = async (msgId, modelName, score) => {
  try {
    const answerId = messages.find(m => m.id === msgId)?.modelAnswerIds?.[modelName]
    if (!answerId) return
    
    const res = await updateModelScore(answerId, score)
    if (res.data?.code === 1) {
      ElMessage.success('')
      const allRated = selectedModels.value.every(m => 
        messages.find(msg => msg.id === msgId)?.scores[m] !== null
      )
      if (allRated) {
        scoreSubmitted.value = true  // 
      }
    }
  } catch (error) {
    ElMessage.error(': ' + error.message)
  }
}
// setup
const handleSubmitReason = async (msgId, model) => {
  const currentMsg = messages.find(m => m.id === msgId)
  if (!currentMsg?.modelAnswerIds?.[model] || !currentMsg.reasons[model]?.trim()) return

  try {
    const res = await updateStudentReason({
      id: currentMsg.modelAnswerIds[model],
      studentReason: currentMsg.reasons[model]
    })
    if (res.data?.code === 1) {
      ElMessage.success('')
    }
  } catch (error) {
    ElMessage.error('' + error.message)
  }
}
// 
const showModelSelector = ref(true)
// const showAnswerButton = ref(true)



const sendMessage = async () => {  
  if (!canSend.value) return
  
  const msgId = Date.now().toString()
  // 
  // 
  messages.push({
    id: msgId,
    content: question.value,  // 
    responses: Object.fromEntries(selectedModels.value.map(m => [m, '...'])),
    modelAnswerIds: {}, // ID
    scores: Object.fromEntries(selectedModels.value.map(m => [m, null])),
    reasons: Object.fromEntries(selectedModels.value.map(m => [m, ''])),
    hasAllResponses: false,
    allRated: computed(() => selectedModels.value.every(m => messages.find(msg => msg.id === msgId)?.scores[m] !== null)), // 
    promptShown: false // 
  })
  
  await nextTick()
  scrollToBottom()
  
  isChatting.value = true

  try {
    const res = await multiModelChat(
      selectedModels.value,
      question.value,
      Number(userId.value), // 
      Number(route.params.courseId),
      Number(route.params.chapterId),
      Number(questionId.value) // 
    )
    
    if (res.data?.code === 1) {
      const currentMsg = messages.find(m => m.id === msgId)
      res.data.data?.forEach(item => {
        if (currentMsg && item.modelName && item.response) {
          currentMsg.responses[item.modelName] = item.response
          currentMsg.modelAnswerIds[item.modelName] = item.id // ID
          currentMsg.hasAllResponses = selectedModels.value.every(m => 
            !currentMsg.responses[m].includes('...')
          )
        }
      })
    } else {
      ElMessageBox.confirm(
        ': ' + (res.data?.message || '') + '',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'error'
        }
      ).then(() => {
        location.reload()
      })
    }
  } catch (error) {
    console.error(':', error)
    ElMessage.error(`: ${error.message}`)
  } finally {
    isChatting.value = false
    scrollToBottom()
  }
}

// 
const getModelResponse = (msgId, model) => {
  const msg = messages.find(m => m.id === msgId)
  return msg ? msg.responses[model] : ''
}

// 
const canSend = computed(() => {
  return selectedModels.value.length > 0 && !isChatting.value
})

// 
// 
const fetchModels = async () => {
  try {
    const res = await getAvailableModels(route.params.courseId)  // courseId
    availableModels.value = res.data.data || []
  } catch (error) {
    ElMessage.error('')
  }
}

// 
const messageBox = ref(null)
const scrollToBottom = () => {
  if (messageBox.value) {
    messageBox.value.scrollTop = messageBox.value.scrollHeight
  }
}

// Markdown
import { marked } from 'marked'
import hljs from 'highlight.js'

// Markdown
marked.setOptions({
  highlight: (code) => hljs.highlightAuto(code).value,
  breaks: true
})

// Markdown
const renderMarkdown = (content) => {
  return content ? marked(content) : '<div class="loading-text">...</div>'
}



// 
onMounted(() => {
  if (route.query.question) {
    // 
    sendMessage()
  }
})
// 
const showAnswerButton = ref(true)

// 
const handleAnswer = async () => {
  try {
    await ElMessageBox.confirm(
      ` ${selectedModels.value.length} `,
      '',
      {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      }
    )
    showAnswerButton.value = false
    showModelSelector.value = false
    await sendMessage()
  } catch (error) {
    // 
  }
}



</script>


<style scoped>
/*  */
.model-selection-container {
  background: #f8fafc;
  border-radius: 8px;
  padding: 15px;
  margin-top: 10px;
  border: 1px solid #e2e8f0;
}

.selection-tip {
  color: #64748b;
  font-size: 13px;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  gap: 8px;
}

/*  */
.model-checkbox-group {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.model-btn {
  padding: 10px 18px;
  border-radius: 18px;
  border: 1px solid #e2e8f0;
  transition: all 0.2s;
}

.model-btn.is-checked {
  background: #409eff;
  color: white;
  border-color: #409eff;
  box-shadow: 0 2px 6px rgba(64, 158, 255, 0.2);
}

.back-btn {
  margin-bottom: 20px;
  padding: 10px 20px;
  border-radius: 18px;
  box-shadow: 0 2px 8px rgba(64, 158, 255, 0.2);
  transition: transform 0.3s;
}

.back-btn:hover {
  transform: translateY(-2px);
}
/*  */
.compact-btn {
  padding: 8px 20px;
  font-size: 14px;
  width: auto;
  margin: 0 auto;
  display: block;
}

/*  */
.selected-models {
  margin-top: 15px;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.model-tag {
  padding: 8px 12px;
  border-radius: 15px;
  background: #f0f9eb;
  border-color: #e1f3d8;
}
/* CSS */
/*  */
.el-button--success {
  background: linear-gradient(135deg, #67c23a, #85ce61);
  border: none;
  transition: all 0.3s;
}

.el-button--success:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(103,194,58,0.3);
}

.el-button--success .el-icon {
  margin-right: 8px;
  font-size: 18px;
}
.chapter-detail {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.model-selector {
  margin-bottom: 20px;
}

.model-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.chat-container {
  border: 1px solid #ebeef5;
  border-radius: 4px;
  background: #f8f9fa;
}

.message-list {
  height: 60vh;
  padding: 20px;
  overflow-y: auto;  /*  */
  scroll-behavior: smooth;  /*  */
}

.message-item {
  margin-bottom: 30px;
}

.user-message {
  display: flex;
  align-items: start;
  margin-bottom: 20px;
}

.avatar {
  font-size: 28px;
  margin-right: 15px;
  color: #409eff;
}

.content-box {
  flex: 1;
}

.message-content {
  background: #fff;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.ai-responses {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}

.response-card {
  transition: transform 0.3s;
}

.response-card:hover {
  transform: translateY(-3px);
}

.model-name {
  font-weight: bold;
  color: #409eff;
}

.response-content {
  min-height: 100px;
  line-height: 1.6;
}

.input-area {
  padding: 20px;
  background: #fff;
  border-top: 1px solid #ebeef5;
  display: flex;
  gap: 15px;
}

.send-btn {
  width: 120px;
  height: auto;
}

/* Markdown */
.response-content :deep(pre) {
  background: #f6f8fa;
  padding: 12px;
  border-radius: 4px;
  margin: 10px 0;
}

.response-content :deep(code) {
  font-family: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, monospace;
  font-size: 14px;
}

.response-content :deep(.hljs) {
  background: transparent;
}

.response-content :deep(strong) {
  color: #409eff;
}
/*  */
.rating-tip {
  color: #909399;
  font-size: 12px;
  margin-bottom: 8px;
}

.number-rating {
  display: flex;
  gap: 6px;
}

:deep(.el-radio-button__inner) {
  padding: 8px 12px;
  border-radius: 4px;
  transition: all 0.2s;
}

:deep(.el-radio-button__orig-radio:checked + .el-radio-button__inner) {
  background: #409eff;
  border-color: #409eff;
  color: white;
}

.improve-card {
  margin-top: 20px;
  transition: all 0.3s;
}

.submit-improve {
  margin-top: 15px;
  width: 200px;
}


/*  */
.improve-card .chat-container {
  margin-top: 15px;
  border: 1px solid #ebeef5;
  border-radius: 8px;
  padding: 10px;
}

.chat-messages {
  height: 300px;
  overflow-y: auto;
  margin-bottom: 10px;
}

.message {
  display: flex;
  margin: 10px 0;
  padding: 8px;
  border-radius: 6px;
}

.user {
  background: #f0f4ff;
}

.assistant {
  background: #f6ffed;
}

.role-icon {
  font-size: 20px;
  margin-right: 10px;
}

.chat-input {
  margin-top: 15px;
}

</style>
